import alcohol from "../Archivos/Imagenes/alcohol.png"
import hielo from "../Archivos/Imagenes/hielo.png"
import disfraz from "../Archivos/Imagenes/disfraz.png"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import foto1 from "../Archivos/Imagenes/foto1.jpeg"
import foto4 from "../Archivos/Imagenes/foto4.jpeg"
import foto5 from "../Archivos/Imagenes/foto5.jpeg"

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cards";


// import required modules
import { EffectCards } from "swiper";


export const Informacion = () =>{
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
       <section  className="category section" id="info">


        <div className="shape__small"></div>
        <h2 className="section__title">
            INFORMACIÓN IMPORTANTE
        </h2>
       
            
            <br></br>
               
                <ul data-aos="slide-left">
                    <li>
                    <p className="home__description">Direccion: Se envia post Inscripción</p>
                    </li>
                    <li>
                    <p className="home__description">Sabado 5 de Noviembre</p>
                    </li>
                    <li>
                    <p className="home__description">Hora de ingreso: 00:00</p>
                    </li>
                    <li>
                    <p className="home__description">Hora de egreso: Cuando se vayan todos!!</p>
                    </li>
                </ul>
            <br></br>
            <hr></hr>
            <br></br>
            <h3 className="section__title">Que tengo que llevar?</h3>

        <div data-aos="fade-up-right"  className="category__container container grid">
            <div className="category__card">
                <img src={disfraz} alt="categoryimage" className="category__img"></img>
                <h3 className="category__title">Disfraz</h3>
                <p className="cateogry__description">Lo mas importante de todo y obligatorio, venir disfrazado</p>
            </div>
            <div className="shape__small"></div>
            <div className="category__card">
                <img src={alcohol} alt="categoryimage" className="category__img"></img>
                <h3 className="category__title">Alcohol</h3>
                <p className="cateogry__description">Traete tu bebida alcoholica o analcohólica que vayas a ingerir</p>
            </div>
            <div className="category__card">
                <img src={hielo} alt="categoryimage" className="category__img"></img>
                <h3 className="category__title">Hielo</h3>
                <p className="cateogry__description">Si podes traer hielo genial, asi no tomamos pichi de Athena!</p>
            </div>
        </div>
        <br></br>
            <hr></hr>
            <br></br>
            <>
            <h3 className="section__title">Fiesta del año pasado</h3>
      <Swiper data-aos="zoom-in"
        effect={"cards"}
        grabCursor={true}
        modules={[EffectCards]}
        className="mySwiper"
      >
        <SwiperSlide><img src={foto1}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        <SwiperSlide><img src={foto4}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        <SwiperSlide><img src={foto4}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        <SwiperSlide><img src={foto4}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        <SwiperSlide><img src={foto4}></img></SwiperSlide>
        <SwiperSlide><img src={foto5}></img></SwiperSlide>
        
      </Swiper>
    </>
       </section>


    )
}