import React from "react"

import logo from "../Archivos/Imagenes/logo-skull.png"
import logoo from "../Archivos/Imagenes/nav-bat.png"

export const NavBar = () => {
 /*=============== SHOW MENU ===============*/
const navMenu = document.getElementById('nav-menu'),
navToggle = document.getElementById('nav-toggle'),
navClose = document.getElementById('nav-close')

/*===== MENU SHOW =====*/
/* Validate if constant exists */
if(navToggle){
navToggle.addEventListener('click', () =>{
  navMenu.classList.add('show-menu')
})
}

/*===== MENU HIDDEN =====*/
/* Validate if constant exists */
if(navClose){
navClose.addEventListener('click', () =>{
  navMenu.classList.remove('show-menu')
})
}

/*=============== REMOVE MENU MOBILE ===============*/
const navLink = document.querySelectorAll('.nav__link')

const linkAction = () =>{
const navMenu = document.getElementById('nav-menu')
// When we click on each nav__link, we remove the show-menu class
navMenu.classList.remove('show-menu')
}
navLink.forEach(n => n.addEventListener('click', linkAction))
    return(
<header className="header" id="header">
<nav className="nav container">
    <a href="#inicio" className="nav__logo">
        
        <img src={logo} alt='logoskull' ></img>Orazzini Halloween Party
    </a>
    <div className="nav__menu" id="nav-menu">
        <ul className="nav__list">
            <li className="nav__item">
                <a href="#fiesta" className="nav__link">Fiesta</a>
            </li>

            <li className="nav__item">
                <a href="#info" className="nav__link">Información</a>
            </li>

            <li className="nav__item">
                <a href="#inscripcion" className="nav__link">Inscripción</a>
            </li>
        </ul>
        <div className="nav__close" id="nav-close">
        <i class="ri-close-line"></i>
        </div>
        <img src={logoo} alt="logobat" className="nav__img"></img>
        </div> 
        <div className="nav__toggle" id="nav-toggle"><i class="ri-menu-4-line"></i></div>
           
</nav>
</header>

    )
}