import React from "react"
import foto1 from "../Archivos/Imagenes/stars.png"
import foto2 from "../Archivos/Imagenes/home-pumpkin.png"
import foto3 from "../Archivos/Imagenes/home-tree1.png"
import foto4 from "../Archivos/Imagenes/home-tree2.png"


export const Fiesta = () =>{
    return (
        <section data-aos="flip-up" className="home section" id="fiesta">
            <div className="shape__small"></div>
            <div className="shape__big"></div>
            
            <div className="home__container container grid">
                <div className="home__data">
            <h1 className="home__title"><span>Halloween Fest</span>2022 edition
            <img src={foto1} alt='homeimage' className="home__title-img-1"></img>
            <img src={foto1} alt='homeimage' className="home__title-img-2"></img>
            </h1>
            <p className="home__description">
                Este Halloween te invito a que te mames responsablemente en mi morada!
            </p>
            
            </div>
            <img className="home__img" src={foto2} alt="homeimage"></img>
            <img src={foto3} alt='homeimage' className="home__tree1"></img>
            <img src={foto4} alt='homeimage' className="home__tree2"></img>
            
            </div>
            </section>
           
        
    )
}